.background {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #010b1a; }

.backimg {
  position: absolute;
  width: 100%;
  top: 50%;
  -webkit-transform: translate(15%, -50%);
          transform: translate(15%, -50%); }
  .backimg.out {
    opacity: 0;
    -webkit-transition: opacity ease 1s;
    transition: opacity ease 1s; }
  .backimg.this {
    opacity: 1;
    -webkit-transition: opacity ease 1s;
    transition: opacity ease 1s; }
  .backimg:first-child {
    width: 100%;
    position: absolute;
    left: 0;
    top: 100%;
    -webkit-transform: translate(0, -100%);
            transform: translate(0, -100%);
    z-index: 999; }
    .backimg:first-child.out {
      opacity: 0;
      -webkit-transition: opacity 0.2s;
      transition: opacity 0.2s; }
    .backimg:first-child.this {
      opacity: 1;
      -webkit-transition: none;
      transition: none; }
  .backimg:last-child.out {
    opacity: 0;
    -webkit-transition: opacity ease 0.1s;
    transition: opacity ease 0.1s; }

@-webkit-keyframes flicker {
  10% {
    opacity: 1; }
  13% {
    opacity: 0.9; }
  16% {
    opacity: 1; }
  17% {
    opacity: 0.7; }
  18% {
    opacity: 1; }
  19% {
    opacity: 0.9; }
  20% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes flicker {
  10% {
    opacity: 1; }
  13% {
    opacity: 0.9; }
  16% {
    opacity: 1; }
  17% {
    opacity: 0.7; }
  18% {
    opacity: 1; }
  19% {
    opacity: 0.9; }
  20% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@media screen and (max-width: 700px) {
  .backimg {
    position: absolute;
    width: 200%;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
    .backimg.out {
      opacity: 0;
      -webkit-transition: opacity 1s;
      transition: opacity 1s; }
    .backimg.this {
      opacity: 1;
      -webkit-transition: opacity 1s;
      transition: opacity 1s; }
    .backimg:first-child {
      position: absolute;
      width: auto;
      height: 100%;
      left: 0;
      top: 0;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
